.viewfinder {
 background: linear-gradient($colorDegradadoAzul, $colorDegradadoAzulFin);
 display: flex;
 justify-content: center;
 align-items: center;
 height: 475px;

 &__card {
   position: relative;
   background-color: $backgroundHeader;
   height: 367px;
 }

 &__card-title {
   display: flex;

   &-titlebox {
     display: flex;
     flex-direction: column;
   }

   &-rectangule {
     height: 42px;
     width: 5px;
     margin: 20px 15px 0 0;
   }

   &-name {
     padding: 20px 14px 5px 0;
     font-family: ‘Comic Sans MS’, ‘Comic Sans’, ‘TSCu_Comic’, sans-serif; /*ojo mirar la fuente bien*/
     font-size: 18px;
     line-height: 20px;
   }

   &-job {
     padding: 0 14px 5px 0;
     font-family: ‘Comic Sans MS’; /*ojo mirar la fuente bien*/
     font-size: 18px;
     line-height: 20px;
   }
 }

 &__icons {
   display: flex;
   justify-content: flex-start;
   margin: 10px 0 10px 10px;

   &-box {
     margin-left: 10px;
     border-radius: 22px;
     width: 32px;
     height: 32px;
     display: flex;
     justify-content: center;
     align-items: center;
   }
 }

 &__tags {
   display: flex;
   justify-content: flex-start;
   align-items: center;
   margin: 5px 20px;

   &-text {
     width: 36px;
     height: 18px;
     font-size: 13px;
     border-radius: 4px;
     margin-right: 10px;
     display: flex;
     justify-content: center;
     align-items: center;
     text-transform: uppercase;
     padding: 0 5px;
   }
 }

 &__button {
   position: absolute;
   top: -40px;
   left: 0;
   width: 75px;
   height: 30px;

   &-reset {
     background-color: $colorBackgroundReset;
     border-radius: 15px;
     border: 1px solid $colorTextTagsCard;
     text-transform: uppercase;
     font-family: ‘OpenSans’, serif;
     font-size: 12px;
     color: $colorTextButtonReset;
     padding: 5px 10px;
   }

   &-icono {
     margin-right: 5px;
     width: 11px;
     height: 20px;
   }
 }

 &.viewfinder--blue {
   .viewfinder__card-title-rectangule {
     background-color: $colorBackgroundRectangule;
   }

   .viewfinder__card-title-name {
     color: $colorTextTitleName;
   }

   .viewfinder__card-title-job {
     color: $colorTextTitleJob;
   }

   .viewfinder__icons-box {
     border: 2px solid  $colorBorderIcons;
     color: $colorBackgroundIcons;
   }

   .viewfinder__tags-text {
     background-color: $colorBackgroundTags;
     color: $colorTextTagsCard;
   }
 }

 &.viewfinder--red {
   .viewfinder__card-title-rectangule {
     background-color: $colorSelectorRedThree;
   }

   .viewfinder__card-title-name {
     color: $colorSelectorRedOne;
   }

   .viewfinder__card-title-job {
     color: $colorTextTitleJob;
   }

   .viewfinder__icons-box {
     border: 2px solid  $colorSelectorRedThree;
     color: $colorSelectorRedOne;
   }

   .viewfinder__tags-text {
     background-color: $colorSelectorRedTwo;
     color: $colorTextTagsCard;
   }
 }

 &.viewfinder--grey {
   .viewfinder__card-title-rectangule {
     background-color: $colorSelectorGreyThree;
   }

   .viewfinder__card-title-name {
     color: $colorSelectorGreyOne;
   }

   .viewfinder__card-title-job {
     color: $colorTextTitleJob;
   }

   .viewfinder__icons-box {
     border: 2px solid  $colorSelectorGreyThree;
     color: $colorSelectorGreyOne;
   }

   .viewfinder__tags-text {
     background-color: $colorSelectorGreyTwo;
     color: $colorTextTagsCard;
   }
 }
}
